@import "variables";

.tg-header-slider {
	position: relative;

	&__item {
		padding: 50px 0;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;
		text-align: center;
		color: #ffffff;
		min-height: 440px;

		@media (min-width: 768px) {
			min-height: 665px;
		}

		&_v3 {
			@media (min-width: 992px) {
				align-items: flex-end;
			}
		}

		p {
			margin-bottom: 0;
		}

		&-section {
			max-width: 302px;
			margin-left: auto;
			margin-right: auto;
		}
	}

	.tg-swiper-pagination {
		position: absolute;
		top: 20px;
		right: 20%;
		bottom: auto;
		left: auto;
		width: auto;
		display: flex;
		//flex-wrap: wrap;
		z-index: 999;

		@media (max-width: 991px) {
			top: auto;
			bottom: 20px;
			left: 50%;
			right: auto;
			transform: translateX(-50%);
		}

		.swiper-pagination-bullet {
			display: block;
			width: 36px;
			height: 7px;
			background-color: $brand-secondary-lite;
			margin: 0 8px;
			border-radius: 0;
			opacity: 1;

			@media (min-width: 992px) {
				width: 40px;
			}

			&:first-child {
				margin-left: 0;
			}

			&:last-child {
				margin-right: 0;
			}

			&-active {
				background-color: #858688;
			}
		}
	}
}

.tg-info-block-one {
	width: 100%;
	max-width: 1366px;
	margin-right: auto;
	margin-left: auto;

	@media (min-width: 1200px) {
		padding-right: 45px;
		padding-left: 45px;
	}

	&__text {
		flex: 0 0 100%;
		max-width: 100%;
		padding: 30px 15px;

		@media (min-width: 768px) {
			flex: 0 0 50%;
			max-width: 50%;
			padding: 85px 15px 0;
		}

		@media (min-width: 992px) {
			flex: 0 0 470px;
			max-width: 470px;
			padding: 85px 0 0;
		}

		@media (min-width: 1310px) {
			margin-left: 225px;
		}
	}

	&__jp {
		max-width: 400px;
		margin-left: auto;
		margin-right: auto;
		position: relative;

		@media (min-width: 768px) {
			flex: 0 0 50%;
			max-width: 50%;
		}

		@media (min-width: 992px) {
			flex: 0 0 495px;
			max-width: 495px;
		}

		&-img {
			width: 100%;

			@media (min-width: 1200px) {
				position: absolute;
				right: 65px;
				bottom: 20px;
				max-width: 428px;
			}

			img {
				width: 100%;

				@media (max-width: 1199px) {
					border-bottom: 25px solid $brand-primary;
				}
			}
		}

		&-layout {
			margin-left: auto;
			width: 343px;
			height: 462px;
			background-color: $brand-primary;
			display: none;

			@media (min-width: 1200px) {
				display: block;
			}
		}
	}
}

.tg-block-help {
	background-color: $brand-secondary;

	&__item {
		margin-left: -15px;
		margin-right: -15px;
		padding: 7px 9px;
		background-color: #ffffff;
		box-shadow: 3px 3px 6px 0 rgba(0, 0, 0, 0.1);

		@media (min-width: 576px) {
			max-width: 203px;
			min-height: 170px;
			width: 100%;
			height: 100%;
			margin-left: auto;
			margin-right: auto;
		}

		@media (max-width: 575px) {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			padding: 7px 30px;
		}

		&-icon {
			width: 76px;
			height: 76px;
			background-position: center center;
			background-repeat: no-repeat;
			background-size: cover;
			margin-bottom: 10px;

			@media (min-width: 576px) {
				margin-left: auto;
				margin-right: auto;
			}

			@media (max-width: 575px) {
				flex: 0 0 76px;
				max-width: 76px;
				margin-bottom: 0;
			}
		}

		&-title {
			text-align: center;
			font-weight: bold;
			font-size: rem(18);
			line-height: 1.2;
			text-transform: uppercase;

			@media (max-width: 575px) {
				flex: 0 0 205px;
				max-width: 205px;
				margin: 0 auto;
			}
		}
	}
}

.tg-explore {
	background-color: $brand-primary;
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
	background-image: url(../img/mask-1.png);
	color: #ffffff;
	position: relative;


	&__item {
		max-width: 185px;
		margin-left: auto;
		margin-right: auto;
		padding-top: 7px;
	}

	&__icon {
		margin-left: 18px;
		margin-right: 13px;
		background-color: #fff;
		border-radius: 0 70px 0 0;
		padding: 18px 15px 15px;
		position: relative;
		margin-bottom: 11px;

		&:before {
			content: '';
			position: absolute;
			top: -7px;
			right: -11px;
			width: 73px;
			height: 62px;
			background-position: center center;
			background-size: cover;
			background-repeat: no-repeat;
			background-image: url(../img/subtraction_1.png);
		}

		&-img {
			width: 77px;
			height: 77px;
			background-repeat: no-repeat;
			background-position: center center;
			background-size: cover;
			margin-left: auto;
			margin-right: auto;
		}
	}

	&__logo {
		position: relative;
		height: 29px;
		padding-left: 18px;
		margin-bottom: 5px;

		&:before {
			content: '';
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			width: 7px;
			background-color: #000000;
		}

		img {
			max-height: 100%;
			width: auto;
		}
	}

	&__text {
		font-size: rem(16);
		line-height: 1.1;
		padding-left: 18px;
		padding-right: 13px;
	}
}

.tg-expert {

	&__item {
		border-left: 8px solid $brand-primary;

		img {
			width: 100%;
		}
	}
}

.tg-ready {
	background-color: $brand-primary;
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
	background-image: url(../img/mask-2.png);
	color: #ffffff;
	position: relative;

	p {
		margin-bottom: 0;
	}
}